import FavoriteRestaurantIDB from '../../data/favorite-resturant-idb';
import Alerts from '../../utils/alerts';

const Favorite = {
  async render() {
    return `
      <section id="main-content">
        <h1 class="title" tabindex="0">Restoran Favoritku</h1>
        <resto-list></resto-list>
      </section>
    `;
  },

  async afterRender() {
    try {
      Alerts.showLoading();
      const listRestaurans = await FavoriteRestaurantIDB.getAllRestaurants();
      const restaurantsContainer = document.querySelector('resto-list');
      if (listRestaurans.length > 0) {
        restaurantsContainer.setRestoList(listRestaurans);
      } else {
        restaurantsContainer.innerHTML =
          '<p class="restaurants_not_found">Tidak ada restoran favorit yang disimpan.</p>';
      }
      Alerts.closeLoading();
    } catch (error) {
      Alerts.showError(error);
    }
  },
};

export default Favorite;
