import DicodingRestauranSource from '../../data/dicodingRestauran-source';
import Alerts from '../../utils/alerts';

const ListRestaurants = {
  async render() {
    return `
    <main-hero></main-hero>
      <section id="main-content">
        <h1 class="title" tabindex="0">Jelajahi Restoran</h1>
        <resto-list></resto-list>
      </section>
    `;
  },

  async afterRender() {
    try {
      Alerts.showLoading();
      const listRestaurans = await DicodingRestauranSource.listRestaurants();
      const restaurantsContainer = document.querySelector('resto-list');
      restaurantsContainer.setRestoList(listRestaurans);
      Alerts.closeLoading();
    } catch (error) {
      console.log(error);
      Alerts.showError(error);
    }
  },
};

export default ListRestaurants;
