import UrlParser from '../../routes/url-parser';
import DicodingRestauranSource from '../../data/dicodingRestauran-source';
import LikeButtonPresenter from '../../utils/like-button-presenter';
import Alerts from '../../utils/alerts';
import FavoriteRestaurantIDB from '../../data/favorite-resturant-idb';

const Detail = {
  async render() {
    return `
      <section id="main-content">
        <resto-detail></resto-detail>
      </section>
    `;
  },

  async afterRender() {
    async function loadDetailRestaurant() {
      Alerts.showLoading();
      try {
        const url = UrlParser.parseActiveUrlWithoutCombiner();
        const detailRestauran = await DicodingRestauranSource.detailRestaurant(
          url.id
        );
        const restaurantContainer = document.querySelector('resto-detail');
        restaurantContainer.setRestoDetail(detailRestauran);

        LikeButtonPresenter.init({
          likeButtonContainer: document.querySelector('#likeButtonContainer'),
          restaurant: detailRestauran,
          favoriteRestaurant: FavoriteRestaurantIDB,
        });

        document
          .querySelector('#formReview')
          .addEventListener('submit', (event) => {
            event.preventDefault();
            addReview();
          });

        Alerts.closeLoading();
      } catch (error) {
        Alerts.showError(error);
      }
    }

    loadDetailRestaurant();

    async function addReview() {
      Alerts.showLoading();
      try {
        const { id } = UrlParser.parseActiveUrlWithoutCombiner();
        const inputName = document.getElementById('inputName').value;
        const inputReview = document.getElementById('inputReview').value;

        if (!inputName || !inputReview) {
          throw new Error('Nama atau review tidak boleh kosong');
        }

        await DicodingRestauranSource.postRestaurantReview({
          id,
          name: inputName,
          review: inputReview,
        });
        loadDetailRestaurant();
      } catch (error) {
        Alerts.showError(error.message);
      }
    }
  },
};
export default Detail;
